const getComponentByRootPath = function (rootPath:string):any {
    //,
    if (process.env['NODE_ENV'] !== 'development') {
        return (page:any) => () =>
            import (`@/views${rootPath}/${page}.vue`);
    } else {
        return (page:any) => {
            return require(`@/views${rootPath}/${page}.vue`).default;
        }
    }
};

export {
    getComponentByRootPath
}
