
 /* eslint-disable */
// 
const getUrlParams = function (name: string) {
  const reg = new RegExp('(^|&)' + name + '=([^&]*)(&|$)', 'i')
  const r = window.location.search.substr(1).match(reg)
  if (r != null) {
    return unescape(r[2])
  }
  return null
};
// PC
const isIOS = function () {
  if (/Android|webOS|iPhone|iPod|BlackBerry/i.test(navigator.userAgent)) {
    return true
  } else {
    return false
  }
}
// ios
const testMobileType = function () {
  const u = navigator.userAgent
  const isAndroid = u.indexOf('Android') > -1 || u.indexOf('Adr') > -1 // android
  const isiOS = !!u.match(/\(i[^;]+;( U;)? CPU.+Mac OS X/) // ios
  if (isAndroid) {
    return 'android'
  } else if (isiOS) {
    return 'ios'
  } else {
    return 'pc'
  }
}
// 
const isPhone = function (val: string) {
  const patrn = /^(((1[3456789][0-9]{1})|(15[0-9]{1}))+\d{8})$/
  if (!patrn.test(val) || val === '') {
    return false
  } else {
    return true
  }
}
// 
const isCarNum = function (val: string) {
  const patrn = /^([A-Z]{1}[A-Z]{1}[A-HJ-NP-Z0-9]{4}[A-HJ-NP-Z0-9]{1})$/
  const patrn2 = /^([A-Z]{1}[A-Z]{1}(([0-9]{5}[DF])|([DF]([A-HJ-NP-Z0-9])[0-9]{4})))$/
  if (!patrn.test(val) && !patrn2.test(val)) {
    return false
  } else {
    return true
  }
}
// 
const isVehicle = function (val: string) {
  const patrn = /^[A-HJ-NP-Za-hj-np-z0-9]+$/
  if (!patrn.test(val) || val === '') {
    return false
  } else {
    return true
  }
}
// 
const isSfz = function (idcard: string) {
  const Errors = [1, '', '', '', '']
  const area: any = { 11: '', 12: '', 13: '', 14: '', 15: '', 21: '', 22: '', 23: '', 31: '', 32: '', 33: '', 34: '', 35: '', 36: '', 37: '', 41: '', 42: '', 43: '', 44: '', 45: '', 46: '', 50: '', 51: '', 52: '', 53: '', 54: '', 61: '', 62: '', 63: '', 64: '', 65: 'xinjiang', 71: '', 81: '', 82: '', 91: '' }
  let Y, JYM
  let S, M, ereg, Err
  let arr = []
  arr = idcard.split('')
  if (area[parseInt(idcard.substr(0, 2))] == null) {
    Err = Errors[4]
    return Err
  }
  switch (idcard.length) {
    case 15:
      if ((parseInt(idcard.substr(6, 2)) + 1900) % 4 === 0 || ((parseInt(idcard.substr(6, 2)) + 1900) % 100 === 0 && (parseInt(idcard.substr(6, 2)) + 1900) % 4 === 0)) {
        ereg = /^[1-9][0-9]{5}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}$/ // 
      } else {
        ereg = /^[1-9][0-9]{5}[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}$/ // 
      }
      if (ereg.test(idcard)) {
        Err = Errors[0]
      } else {
        Err = Errors[2]
      }
      return Err
    case 18:
      if (parseInt(idcard.substr(6, 4)) % 4 === 0 || (parseInt(idcard.substr(6, 4)) % 100 === 0 && parseInt(idcard.substr(6, 4)) % 4 === 0)) {
        ereg = /^[1-9][0-9]{5}19[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|[1-2][0-9]))[0-9]{3}[0-9Xx]$/ // 
      } else {
        ereg = /^[1-9][0-9]{5}19[0-9]{2}((01|03|05|07|08|10|12)(0[1-9]|[1-2][0-9]|3[0-1])|(04|06|09|11)(0[1-9]|[1-2][0-9]|30)|02(0[1-9]|1[0-9]|2[0-8]))[0-9]{3}[0-9Xx]$/ // 
      }
      if (ereg.test(idcard)) {
        S = (parseInt(arr[0]) + parseInt(arr[10])) * 7 + (parseInt(arr[1]) + parseInt(arr[11])) * 9 + (parseInt(arr[2]) + parseInt(arr[12])) * 10 + (parseInt(arr[3]) + parseInt(arr[13])) * 5 + (parseInt(arr[4]) + parseInt(arr[14])) * 8 + (parseInt(arr[5]) + parseInt(arr[15])) * 4 + (parseInt(arr[6]) + parseInt(arr[16])) * 2 + parseInt(arr[7]) * 1 + parseInt(arr[8]) * 6 + parseInt(arr[9]) * 3
        Y = S % 11
        M = 'F'
        JYM = '10X98765432'
        M = JYM.substr(Y, 1)
        if (M === arr[17]) {
          Err = Errors[0]
        } else {
          Err = Errors[3]
        }
      } else {
        Err = Errors[2]
      }
      return Err
    default:
      Err = Errors[1]
      return Err
  }
}
// 
const isEmojiCharacter = function (substring: string) {
  for (let i = 0; i < substring.length; i++) {
    const hs = substring.charCodeAt(i)
    if (hs >= 0xd800 && hs <= 0xdbff) {
      if (substring.length > 1) {
        const ls = substring.charCodeAt(i + 1)
        const uc = ((hs - 0xd800) * 0x400) + (ls - 0xdc00) + 0x10000
        if (uc >= 0x1d000 && uc <= 0x1f77f) {
          return true
        }
      }
    } else if (substring.length > 1) {
      const ls = substring.charCodeAt(i + 1)
      if (ls === 0x20e3) {
        return true
      }
    } else {
      if (hs >= 0x2100 && hs <= 0x27ff) {
        return true
      } else if (hs >= 0x2B05 && hs <= 0x2b07) {
        return true
      } else if (hs >= 0x2934 && hs <= 0x2935) {
        return true
      } else if (hs >= 0x3297 && hs <= 0x3299) {
        return true
      } else if (hs === 0xa9 || hs === 0xae || hs === 0x303d || hs === 0x3030 ||
        hs === 0x2b55 || hs === 0x2b1c || hs === 0x2b1b || hs === 0x2b50) {
        return true
      }
    }
  }
}
// 
const dateFormat = function (date: Date, fmt: string) {
  const o: any = {
    "Y+": date.getFullYear(),
    'M+': date.getMonth() + 1,
    'd+': date.getDate(),
    'h+': date.getHours(),
    'm+': date.getMinutes(),
    's+': date.getSeconds(),
    'q+': Math.floor((date.getMonth() + 3) / 3),
    S: date.getMilliseconds()
  }
  if (/(y+)/.test(fmt)) fmt = fmt.replace(RegExp.$1, (date.getFullYear() + '').substr(4 - RegExp.$1.length))
  for (const k in o) {
    if (new RegExp('(' + k + ')').test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length === 1) ? (o[k]) : (('00' + o[k]).substr(('' + o[k]).length)))
  }
  return fmt
}
// 
const TimeFormat = function (val: number, s: boolean) {
  if (!val) {
    return ''
  }
  if (s) {
    val = val * 1000
  }
  const date: any = new Date(val)
  const year: any = date.getFullYear()
  let month: any = date.getMonth() + 1
  let day: any = date.getDate()
  let hour: any = date.getHours()
  let min: any = date.getMinutes()
  let second: any = date.getSeconds()
  month = month < 10 ? '0' + month : month
  day = day < 10 ? '0' + day : day
  hour = hour < 10 ? '0' + hour : hour
  min = min < 10 ? '0' + min : min
  second = second < 10 ? '0' + second : second
  return year + '-' + month + '-' + day + ' ' + hour + ':' + min + ':' + second
}
// 
const getDate = function (n: any) {
  const date = new Date()
  date.setDate(date.getDate() + n)
  const year: any = date.getFullYear()
  let month: any = date.getMonth() + 1
  let day: any = date.getDate()
  month = month < 10 ? '0' + month : month
  day = day < 10 ? '0' + day : day
  return year + '-' + month + '-' + day
}
// 
const formatMomey = function (num: any) {
  return Math.round(num * 100) / 100
}
// 
const dealImage = function (path: any, obj: any, callback: any) {
  const img = new Image()
  img.src = path
  img.onload = function () {
    // const that = this;
    // 
    let w = 100;
    let h = 100;
    const scale = w / h
    w = obj.width || w
    h = obj.height || w / scale
    let quality = 0.5 // 0.7
    // canvas
    const canvas = document.createElement('canvas')
    const ctx: any = canvas.getContext('2d')
    // 
    const anw: any = document.createAttribute('width')
    anw.nodeValue = w
    const anh: any = document.createAttribute('height')
    anh.nodeValue = h
    canvas.setAttributeNode(anw)
    canvas.setAttributeNode(anh)
    ctx.drawImage(this, 0, 0, w, h)
    // 
    if (obj.quality && obj.quality <= 1 && obj.quality > 0) {
      quality = obj.quality
    }
    // quality
    const base64 = canvas.toDataURL('image/jpeg', quality)
    // base64
    callback(base64)
  }
}
// base64
const getImgData = function (img: any, dir: any, next: any) {
  const image = new Image()
  image.onload = function () {
    let degree = 0
    let drawWidth
    let drawHeight
    let width
    let height
    // drawWidth = this.naturalWidth
    // drawHeight = this.naturalHeight
    drawWidth = 100
    drawHeight = 100
    // 
    const canvas: any = document.createElement('canvas')
    canvas.width = width = drawWidth
    canvas.height = height = drawHeight
    const context: any = canvas.getContext('2d')
    // canvasiphonehome
    switch (dir) {
      // iphonehome
      case 3:
        degree = 180
        drawWidth = -width
        drawHeight = -height
        break
      // iphonehome()
      case 6:
        canvas.width = height
        canvas.height = width
        degree = 90
        drawWidth = width
        drawHeight = -height
        break
      // iphonehome
      case 8:
        canvas.width = height
        canvas.height = width
        degree = 270
        drawWidth = -width
        drawHeight = height
        break
    }
    // canvas
    context.rotate((degree * Math.PI) / 180)
    context.drawImage(this, 0, 0, drawWidth, drawHeight)
    // 
    next(canvas.toDataURL('image/jpeg', 0.8))
  }
  image.src = img
}
// base64Blob
const base64ToBlob = function(base64Data: any) {
  const arr = base64Data.split(",");
  const fileType = arr[0].match(/:(.*?);/)[1];
  const bstr = atob(arr[1]);
  let l = bstr.length;
  const u8Arr = new Uint8Array(l);

  while (l--) {
    u8Arr[l] = bstr.charCodeAt(l);
  }
  return new Blob([u8Arr], {
    type: fileType,
  });
}
// base64File
const base64ToFile = function(base64Data: any, fileName: string) {
  let newBlob:any = base64ToBlob(base64Data);
  newBlob.lastModifiedDate = new Date();
  newBlob.name = fileName;
  return newBlob;
}

const storage:any = window.localStorage;

const setStorage = function (key:string = 'property',data:any):void {
    storage[key] = JSON.stringify(data);
};

const getStorage = function (key:string = 'property'):any {
    return JSON.parse(storage.getItem(key));
}

const clearStorage = function (key:string = 'property') {
    storage[key] = '[]';
}

export {
  getUrlParams,
  isIOS,
  testMobileType,
  isPhone,
  isCarNum,
  isVehicle,
  isSfz,
  isEmojiCharacter,
  dateFormat,
  TimeFormat,
  getDate,
  formatMomey,
  dealImage,
  getImgData,
  base64ToBlob,
  base64ToFile,
  setStorage,
  getStorage,
  clearStorage
}
